<template xmlns:th="http://www.w3.org/1999/xhtml">
  <div>
    <div class="header fixed-top" th:insert="fragments/header :: header"></div>
    <div class="text-center all main-section">
      <b-container>
        <h1 class="display-4">Znajdź swoje miejsce</h1>
        <div class="search-box mx-auto text-center w-75">
          <b-form>
            <b-form-group id="citySearch" horizontal label="Miasto" label-for="cityInput">
              <b-form-input v-model="searchInput" id="cityInput" class="col-xs-2" type="text" size="lg"
                            placeholder="Wpisz miasto"
                            required></b-form-input>
            </b-form-group>
            <div class="h-40 d-inline-block"></div>
            <div class="text-right">
              <b-button th:type="submit" v-on:click="routeToList" size="lg" variant="primary">Szukaj</b-button>

            </div>
          </b-form>
        </div>
      </b-container>

    </div>
    <div class="footer fixed-bottom" th:insert="fragments/footer :: footer"></div>
  </div>
</template>

<script>
/* eslint-disable */
  export default {
    name: 'Search',
    data() {
      return {
        searchInput: '',
        }},
        methods: {
            routeToList: function (event) {
            this.$router.replace({ path:'/recyclerview', query: { search: this.searchInput}})
            }
        }
  }
</script>

<style>

  .all {
    min-height: 100%;
    /*width: 100%;*/
    background: url('http://www.hamstech.com/blog/wp-content/uploads/2018/01/interior-design-course-1024x677.jpeg') no-repeat;
    background-size: cover;

    display: flex;
    align-items: center;

  }

  .search-box {
    margin-top: 40px;
    padding: 50px 80px 30px 30px;
    margin-bottom: 100px;
    border-radius: 5px;
    background-color: rgba(230, 247, 255, .7);
  }

  .main-section {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 1;
  }

  .header {
    z-index: 999;
  }

  .footer {
    z-index: 999;
  }
</style>



